aside#sidebar {
    .main-section & {
        @media only screen and (min-width: 1px) {
          .background-lt-green;
          padding: 20px;

          select {
            .roboto-slab;
            .color-white;
            font-weight: 400;
            .font-1;
            width: 100%;
            outline: none !important;
            .lh-appearance(none);
            border: none;
            background: #95d759 url('../images/chevrons/chevron-dropdown-bg.png') 95% center no-repeat;
            background-size: 20px 15px;
            padding: 5px 5px 5px 15px;
            .lh-border-radius(0);
          }
        }

        @media only screen and (min-width: @screen-sm-min) {
        }

        @media only screen and (min-width: @screen-md-min) {
          .background-transparent;
          ul{
            list-style: none;
            padding: 0;
            margin-left: 20px;
            
            li{
              .color-lt-green;

              a{
                .color-lt-green;

                .chevron{
                  position: absolute;
                  left: 13px;
                }
              }
            }
          }
        }

        @media only screen and (min-width: @screen-lg-min) {
        }
    }
}