#sidebar-promotion{
    @media only screen and (min-width: 1px) {
        width: 100%;
        padding: 30px;
        .background-lt-green;
        border-width: 8px;
        border-style: solid;
        -webkit-border-image:
          -webkit-linear-gradient(left, @lt-grey 35%, @white 50%, @white 40%, @lt-grey 65%) 100 100;
        -moz-border-image:
          -moz-linear-gradient(left, @lt-grey 35%, @white 50%, @white 40%, @lt-grey 65%) 100 100;
        -o-border-image:
          -o-linear-gradient(left, @lt-grey 35%, @white 50%, @white 40%, @lt-grey 65%) 100 100;
        border-image:
          linear-gradient(to right, @lt-grey 35%, @white 50%, @white 40%, @lt-grey 65%) 100 100;

      .promo{
        text-align: center;

        h3{
          margin-top: 0;
          .color-white;
          font-weight: 600;
        }

        .image{
          img{
            max-width: 80%;
          }
        }

        .content{
          margin: 15px 0;

          p{
            text-align: center;
            font-weight: 600;
          }
        }

        .btn-container{
          .btn {
            padding: 10px 15px 10px 25px;

            .btn-text {
              .font-3;
              text-transform: uppercase;
              position: relative;
              top: 3px;
            }
          }
        }
      }

    }

    @media only screen and (min-width: @screen-sm-min) {
    }

    @media only screen and (min-width: @screen-md-min) {
    }

    @media only screen and (min-width: @screen-lg-min) {
    }
}