section.main-section{

    body.books-resources & {

        @media only screen {
            #books-resources-results{
                .roboto-slab;

                .row {
                    padding-bottom: 25px;
                    border-bottom: 1px solid @ltst-grey;
                    margin-bottom: 25px;

                    &:last-of-type{
                        border-bottom: none;
                        padding-bottom: none;
                    }

                    .image {
                        img {
                            max-width: 100%;
                        }
                    }

                    .title-description {
                        .title {
                            .color-blue;
                            font-size: 1.25em;
                            font-weight: 600;
                        }
                    }

                    .price-links {
                        text-align: right;

                        .price {
                            .color-lt-green;
                            font-size: 1.5em;
                            font-weight: 400;
                            margin-bottom: 0.5em;
                        }
                    }
                }
            }
        }

        @media only screen and (min-width: @screen-sm-min) {

        }

        @media only screen and (min-width: @screen-md-min) {

        }

        @media only screen and (min-width: @screen-lg-min) {

        }
    }
}