html, body {
    background: @ltst-grey;
}

body {
    .roboto;
    position: relative;
}

header{

    body & {
        @media only screen and (min-width: 1px) {

            .header-logo-container {
                text-align: center;
                .vertical-padding;

                .header-logo {
                    .inline(middle);
                    width: 130px;

                    .logo-mini{
                        display: none;
                    }
                }
            }

            .header-nav-outer {
                background: @dk-blue;
            }

            .primary-nav-outer {
                background: @ltst-grey;

                .primary-nav-container {
                    .horizontal-padding(0);

                    #primary-nav {

                        .ubermenu-responsive-toggle{
                            background: darken(@ltst-grey,5%);
                        }

                        .ubermenu-column, .ubermenu-column-auto{
                            min-width: 100% !important;
                        }

                        .ubermenu-main {
                            background: @ltst-grey;
                            color: @dk-blue;

                            .top-nav-item a {
                                background: @dk-blue;
                                color: @white;
                            }

                            .shopping-cart-icon {
                                background: @dk-blue;

                                a {
                                    display: block;

                                    img {
                                        width: 30px;
                                    }
                                }
                            }

                            .custom-box{

                                .ubermenu-custom-content{
                                    padding: 0 !important;
                                }

                                a{
                                    background: @lt-green;
                                    .roboto-slab;
                                    display: block;
                                    .padding(15px 20px);

                                    .title{
                                        .font-1;
                                        color: @white;
                                    }

                                    .desc{
                                        color: @dk-blue;
                                    }
                                }
                            }

                            .ubermenu-retractor{
                                background: darken(@blue,5%);
                                color: @white;
                            }
                        }
                    }
                }
            }
        }

        @media only screen and (min-width: @screen-sm-min) {
        }

        @media only screen and (min-width: @screen-md-min) {

            position: absolute;
            top: 0;
            left: 0;
            z-index: 9999;
            width: 100%;
            .lh-box-shadow(0 0 2px rgba(0,0,0,0.1));

            .header-logo-container {
                text-align: left;
                padding-bottom: 0;

                .header-logo {
                    width: auto;
                    height: 55px;

                    img{
                        width: auto;
                        height: 100%;
                        .lh-transition(transform .2s ease-in-out);

                        &:hover{
                            .lh-rotate(-10deg);
                        }
                    }
                }
            }

            .header-nav-outer {

                #header-nav {

                    a {
                        padding-top: 10px;
                        padding-bottom: 10px;
                        .lh-transition(background .2s linear);
                    }

                    .shopping-cart-icon {

                        a {
                            display: block;
                            padding-left: 10px;
                            padding-right: 10px;

                            img {
                                width: 20px;
                                display: block;
                            }

                            &:hover{

                                img{
                                    .lh-animation(swing .5s ease-in-out);
                                }
                            }
                        }
                    }
                }
            }

            .primary-nav-outer {

                .primary-nav-container {
                    .horizontal-padding(15px);

                    #primary-nav {

                        .ubermenu.ubermenu-responsive .ubermenu-column, .ubermenu.ubermenu-responsive .ubermenu-column-auto {
                            min-width: initial !important;
                        }

                        .ubermenu-main {
                            background: transparent;
                            margin: 0;
                            .clearfix;

                            .ubermenu-target[tabindex="0"]{
                                padding-top: 35px;
                                padding-bottom: 35px;
                                .lh-transition(background .2s linear);

                                &:hover{
                                    background: lighten(@ltst-grey,2%);
                                }
                            }

                            .custom-box{
                                .pull-right;

                                a {
                                    .lh-transition(background .2s linear);

                                    table {
                                        height: 100%;

                                        .title {

                                        }

                                        .desc {

                                        }
                                    }

                                    &:hover{
                                        background: lighten(@lt-green,5%);
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }

        @media only screen and (min-width: @screen-lg-min) {

            .header-logo-container {

                .header-logo {
                    width: 150px;
                }
            }

            .header-nav-outer {

                #header-nav {

                    a {

                    }
                }
            }

            .primary-nav-outer {

                .container {

                    #primary-nav {

                    }
                }
            }
        }
    }
}

section.main-section{

    body & {
        @media only screen and (min-width: 1px) {

            background: @white;

        }

        @media only screen and (min-width: @screen-sm-min) {
        }

        @media only screen and (min-width: @screen-md-min) {

            padding-top: 121px;

        }

        @media only screen and (min-width: @screen-lg-min) {


        }
    }
}

//STICKY HEADER
body.sticky-header {

    @media only screen and (min-width: @screen-md-min) {

        header {
            position: fixed;

            .header-logo-container {
                padding-top: 5px;
                padding-bottom: 5px;

                .header-logo {
                    height: 35px;

                    .logo-normal {
                        .hide;
                    }

                    .logo-mini {
                        .show;
                    }
                }
            }

            .header-nav-outer {
                display: none;
            }

            .primary-nav-outer {

                .primary-nav-container {

                    #primary-nav {

                        .ubermenu-main {

                            .ubermenu-target[tabindex="0"] {
                                padding-top: 15px;
                                padding-bottom: 15px;
                            }
                        }
                    }
                }
            }
        }
    }
}

//ANIMATION FOR STICKY HEADER
body.sticky-animation{

    @media only screen and (min-width: @screen-md-min) {

        .header-logo-container {

            .header-logo {
                .lh-transition(height .2s ease-in);
            }
        }

        .primary-nav-outer {

            .primary-nav-container {

                #primary-nav {

                    .ubermenu-main {

                        .ubermenu-target[tabindex="0"] {
                            .lh-transition(padding .2s ease-in);
                        }
                    }
                }
            }
        }
    }
}

footer{

    body & {
        @media only screen and (min-width: 1px) {

            background: @ltst-grey;
            .font-size(12px,16px);

            .container{
                .vertical-padding;
                .roboto-slab;

                h3{
                    margin-top: 0;
                    color: @blue;
                    .font-3;
                }

                .link-list{
                    list-style: none;
                    margin: 0;
                    padding: 0;

                    li{

                        a{
                            display: block;
                            .padding(5px 0);
                            color: @dk-blue;
                        }
                    }
                }

                .industries{
                    padding-bottom: 15px;
                }

                .page-links{
                    .font-weight(bold);
                    border-top: 1px solid @white;
                    padding-top: 15px;
                }

                .contact-form{
                    padding-top: 30px;
                    text-align: center;

                    h2{
                        margin: 0;
                        .font-weight(bold);
                        color: @lt-green;
                        .font-4;
                    }

                    .title{
                        .font-2;
                        color: @blue;
                        .font-weight(bold);
                        margin-bottom: 15px;

                        a{
                            .phone-link(@blue);
                        }
                    }

                    .newsletter-form{
                        text-align: left;
                        padding-top: 15px;

                        .form-control{
                            background: @lt-grey;
                            border-color: @white;
                            .form-control-focus(@lt-green);
                        }

                        .btn{
                            width: 100%;
                        }

                        .ajax-loader{
                            margin: 0;
                            padding-top: 3px;
                        }
                    }
                }

                .footer-address-container{
                    text-align: center;

                    .footer-address{
                        color: @grey;
                        padding-bottom: 5px;
                        margin-bottom: 5px;
                        border-bottom: 1px solid @white;
                    }
                }
            }
        }

        @media only screen and (min-width: @screen-sm-min) {

            .container{

                .page-links{
                    padding-top: 0;
                    border-top: 0;
                    .clearfix;

                    .page-list{
                        .pull-left;
                        width: 50%;
                    }
                }
            }
        }

        @media only screen and (min-width: @screen-md-min) {

            .container{

                .industries{
                    padding-bottom: 0;
                }

                .contact-form{
                    padding-top: 0;

                    .newsletter-form{

                        .btn{

                        }
                    }
                }

                .footer-address-container{
                    text-align: left;
                    padding-top: 15px;
                    margin-bottom: 0;
                    border-top: 1px solid @white;

                    .footer-address{
                        padding-bottom: 0;
                        margin-bottom: 0;
                        border-bottom: 0;
                        .inline;
                    }

                    .privacy-policy{
                        .inline;
                        margin-left: 5px;
                    }
                }
            }
        }

        @media only screen and (min-width: @screen-lg-min) {


        }
    }
}
