#banner-promotion{
    @media only screen and (min-width: 1px) {
        .roboto-slab;
        .background-lt-green;
        padding: 2em 0;

        .image{
            img{
                max-width: 100%;
            }
        }
        
        .promo {
            text-align: center;

            .headline {
                .color-white;
                .font-6;
                font-style: italic;
                margin-top: 0;
            }

            .content{
                .font-2;
            }

            .btn-container{
                .btn {
                    padding: 10px 15px 10px 25px;

                    .btn-text {
                        .roboto;
                        .font-3;
                        text-transform: uppercase;
                        position: relative;
                        top: 3px;
                    }
                }
            }
        }
    }

    @media only screen and (min-width: @screen-sm-min) {
    }

    @media only screen and (min-width: @screen-md-min) {
    }

    @media only screen and (min-width: @screen-lg-min) {
    }
}